import Vue from 'vue'
import Router from 'vue-router'

/*引入page页面or组件资源*/
import Home      from '@/pages/home'
import Foo       from '@/pages/foo'
import Me        from '@/pages/me'
import InputTags from '@/pages/inputTags'
import JsFs      from '@/pages/jsFs'


//定义路由
Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path      : '',
            component : Home,
            meta      : {
                title        : 'ADE',
                judgeBrowser : true
            },
        },
        {
            path      : '/foo',
            component : Foo,
            meta      : {
                title        : 'Vue drag screenshot',
                judgeBrowser : true
            },
        },
        {
            path      : '/me',
            component : Me,
            meta      : {
                title        : '我的',
                judgeBrowser : true     
            },
        },
        {
            path      : '/inputTags',
            component : InputTags,
            meta      : {
                title        : 'Vue input tags',
                judgeBrowser : true     
            },
        },
        {
            path      : '/jsFs',
            component : JsFs,
            meta      : {
                title        : 'JS 常用函数',
                judgeBrowser : true     
            },
        },
    ]
})